body {
  max-width: 80em;
  margin-left: auto;
  margin-right: auto;
}

body:after {
  content: " ";
  display: block;
  clear: both;
}

nav {
  width: 25%;
  float: left;
  margin-right: 5.26316%;
}

#starter {
  text-align: center;
}

#gulp-trans-wrapper {
  background-color: white;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  animation-name: gulpcolors;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

#gulp-trans-wrapper img {
  width: 100%;
  height: auto;
}

/* The animation code */
@-webkit-keyframes gulpcolors {
  0% {
    background-color: #ffccf2;
  }
  50% {
    background-color: #ccffcc;
  }
  75% {
    background-color: #21ec72;
  }
  100% {
    background-color: #ffccf2;
  }
}

@-moz-keyframes gulpcolors {
  0% {
    background-color: #ffccf2;
  }
  50% {
    background-color: #ccffcc;
  }
  75% {
    background-color: #21ec72;
  }
  100% {
    background-color: #ffccf2;
  }
}

@-ms-keyframes gulpcolors {
  0% {
    background-color: #ffccf2;
  }
  50% {
    background-color: #ccffcc;
  }
  75% {
    background-color: #21ec72;
  }
  100% {
    background-color: #ffccf2;
  }
}

@keyframes gulpcolors {
  0% {
    background-color: #ffccf2;
  }
  50% {
    background-color: #ccffcc;
  }
  75% {
    background-color: #21ec72;
  }
  100% {
    background-color: #ffccf2;
  }
}
